.lyocell_background {
  height: 50%;
  padding: 5% 0 0 0;
  background-image: url(../img/lyocell_background.png);
  background-size: cover;

  h1 {
    width: 437px;
    margin: 0 0 0 5%;
    text-align: center;
    text-transform: uppercase;
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 75px;
    line-height: 75px;
    background: #40f99b;
    border-radius: 8px;
    color: #000000;

    @media (max-width: 1000px) {
      font-size: 50px;
      width: 313px;
      line-height: 50px;
    }

    @media (max-width: 600px) {
      font-size: 35px;
      width: 220px;
      line-height: 35px;
    }
  }
}

.description {
  margin-top: 26px;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  overflow: hidden;
}

.lenzing-logo {
  width: 300px;
}

.history {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  justify-content: center;

  h1 {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 51px;
    line-height: 63px;
    text-transform: uppercase;
    text-align: left;
    width: 95%;
    color: #000000;

    @media (max-width: 1178px) {
      font-size: 40px;
    }

    @media (max-width: 600px) {
      font-size: 30px;
    }
  }

  p {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    width: 560px;
    height: 366.32px;
    color: #000000;

    @media (max-width: 1178px) {
      width: auto;
      height: auto;
      margin-bottom: 30px;
    }
  }

  em {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;

    color: #40f99b;
  }

  img {
    width: 560px;
    height: 366.32px;

    @media (max-width: 1178px) {
      width: auto;
      height: auto;
    }
  }
}

