.lyocell_avantages {
  margin: 80px 0 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  h1 {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 51px;
    line-height: 63px;
    text-transform: uppercase;
    width: 95%;
    color: #000000;

    @media (max-width: 1178px) {
      font-size: 40px;
    }

    @media (max-width: 600px) {
      font-size: 30px;
    }
  }

  .avantages {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    height: 80%;

    .avantages_type {
      margin: 50px 0 0 0;
      height: auto;
      width: 400px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @media (max-width: 800px) {
        width: auto;
        height: auto;
        margin: 20px 10px;
        justify-content: left;
      }

      .petit {
        height: 80px;
      }

      img {
        height: 100px;
      }
    }

    .avantages_description {
      margin: 20px 0 0 0;
      height: 167px;

      h2 {
        font-family: AdiHaus;
        font-style: normal;
        font-weight: bold;
        font-size: 25px;
        line-height: 31px;

        color: #000000;
      }

      p {
        font-family: AdiHaus;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        width: 100%;
        color: #000000;

        @media (max-width: 1178px) {
          width: auto;
          height: auto;
          margin-bottom: 30px;
        }
      }
    }
  }
}

