.fournisseurs_background {
  margin: 80px 0 0 0;
  height: 50%;
  padding: 5% 0 0 0;
  background-image: url(../img/fournisseurs.png);
  background-size: cover;

  h1 {
    width: 550px;
    margin: 0 0 0 5%;
    text-align: left;
    text-transform: uppercase;
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 75px;
    line-height: 92px;
    background: #40f99b;
    border-radius: 10px;
    color: #000000;

    @media (max-width: 1000px) {
