  width: 300px;
}

.fournisseurs {
  margin: 50px;
  display: flex;
  flex-wrap: wrap;
  width: 95%;
  justify-content: left;

  @media (max-width: 700px) {
    margin: 10px;
  }

  .fournisseurs_text {
    width: 600px;

    @media (max-width: 1199px) {
      width: 95%;
    }

    h1 {
      font-family: AdiHaus;
      font-style: normal;
      font-weight: bold;
      font-size: 51px;
      line-height: 63px;
      text-transform: uppercase;
      text-align: left;
      width: 100%;
      color: #000000;

      @media (max-width: 1178px) {
        font-size: 40px;
      }

      @media (max-width: 600px) {
        font-size: 30px;
      }
    }

    p {
      font-family: AdiHaus;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 22px;
      margin: 20px 0 0 0;
      color: #000000;

      @media (max-width: 1178px) {
        width: auto;
        height: auto;
      }
    }

    em {
      font-family: AdiHaus;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 22px;

      color: #40f99b;
    }
  }

  .fournisseurs_img {
    width: 600px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1199px) {
      margin-top: 30px;
    }
  }
}
